import { usersAPI } from '../API/Api'

const FOLLOW = 'FOLLOW'
const UNFOLLOW = 'UNFOLLOW'
const SET_USERS = 'SET_USERS'
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE'
const SET_TOTAL_USER_COUNT = 'SET_TOTAL_USER_COUNT'
const SET_IS_FETCHING = 'SET_IS_FETCHING'
const TONGLE_FOLLOW_IN_PROGRESS = 'TONGLE_FOLLOW_IN_PROGRESS'

const initialState = {
   users: [],
   pageSize: 10,
   totalUsersCount: 1,
   currentPage: 1,
   isFetching: false,
   followInProgress: []
}

const usersReducer = (state = initialState, action) => {
   switch (action.type) {
      case FOLLOW:
         return {
            ...state,
            users: state.users.map(u => {
               if (u.id === action.userId) {
                  return { ...u, followed: true }
               }
               return u
            })
         }
      case UNFOLLOW:
         return {
            ...state,
            users: state.users.map(u => {
               if (u.id === action.userId) {
                  return { ...u, followed: false }
               }
               return u
            })
         }
      case SET_USERS:
         return { ...state, users: action.users }
      case SET_CURRENT_PAGE:
         return { ...state, currentPage: action.currentPage }
      case SET_TOTAL_USER_COUNT:
         return { ...state, totalUsersCount: action.totalCount }
      case SET_IS_FETCHING:
         return { ...state, isFetching: action.currentState }
      case TONGLE_FOLLOW_IN_PROGRESS:
         return {
            ...state,
            followInProgress: action.currentState ? [...state.followInProgress, action.userId]
               : state.followInProgress.filter(id => id !== action.userId)
         }
      default:
         return state;
   }
}
export const followSuccess = (userId) => ({ type: FOLLOW, userId })
export const unfollowSuccess = (userId) => ({ type: UNFOLLOW, userId })
export const setUsers = (users) => ({ type: SET_USERS, users })
export const setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage })
export const setTotalUsersCount = (totalCount) => ({ type: SET_TOTAL_USER_COUNT, totalCount })
export const setIsFetching = (currentState) => ({ type: SET_IS_FETCHING, currentState })
export const tongleFollowInProgress = (currentState, userId) => ({ type: TONGLE_FOLLOW_IN_PROGRESS, currentState, userId })
export default usersReducer

export const requestUsers = (page, pageSize) => {
   return (dispatch) => {
      dispatch(setIsFetching(true))
      usersAPI.getUsers(page, pageSize).then((data) => {
         dispatch(setCurrentPage(page))
         dispatch(setIsFetching(false))
         dispatch(setUsers(data.items))
         dispatch(setTotalUsersCount(data.totalCount))
      })
   }
}
export const follow = (userId) => {
   return (dispatch) => {
      dispatch(tongleFollowInProgress(true, userId))
      usersAPI.setFollow(userId).then((data) => {
         if (data.resultCode === 0) {
            dispatch(followSuccess(userId))
         }
         dispatch(tongleFollowInProgress(false, userId))
      })
   }
}  
export const unfollow = (userId) => {
   return (dispatch) => {
      dispatch(tongleFollowInProgress(true, userId))
      usersAPI.setUnfollow(userId).then((data) => {
         if (data.resultCode === 0) {
            dispatch(unfollowSuccess(userId))
         }
         dispatch(tongleFollowInProgress(false, userId))
      })
   }
}
